export default {
	data() {
		return {
			system:this.systemInfo(),
			user:this.getUserInfo(),
			listType:0,
			title:'',
			goodslist:[]
		}
	},
	onLoad(option) {
		console.log(option);
		this.listType = parseInt(option.type);
		
		this.postJson({
			path:"/user_order_list.jsp",
			data:{
				uid:this.user.uid,
				listType:this.listType
			},
			success:(res) => {
				if("ok" == res.data.status){
					this.goodslist = res.data.data;
					for(let x in this.goodslist){
						this.goodslist[x].stateLabel = this.getStateLabel(this.goodslist[x].status);
					}
				}
			}
		});
		
		switch (this.listType){
			case 0:{
				this.title = "待付款"
			}
				break;
			case 2:{
				this.title = "待确认"
			}
				break;
			case 3:{
				this.title = "已完成"
			}
				break;
			case 4:{
				this.title = "退货"
			}
				break;
			case 99:{
				this.title = "全部订单"
			}
				break;
			default:
				break;
		}
	},
	methods: {
		
		getStateLabel:function(state){
			console.log("getStateLabel,state="+state+",type="+typeof(state));
			if(this.listType == 4){
				switch (state) {
					case 0:
					{
						return "退货申请";
					}
						break;
					case 1:
					{
						return "已退货";
					}
						break;
					case 2:
					{
						return "拒绝退货";
					}
						break;
						
					default:
						break;
				}
			}else{
				switch (state) {
					case 0:
					{
						return "待付款";
					}
						break;
					case 1:
					{
						return "待发货";
					}
						break;
					case 2:
					{
					   return "待确认";
					}
						break;
					case 3:
					{
						
						return "已完成";
					}
						break;
					case 4:
					{
						return "已退货";
					}
						break;
					case 5:
					{
						return "已取消";
					}
						break;
					default:
						break;
				}
			}
			
			return "";
		},
		contact(e){
			let index = e.currentTarget.dataset.index;
			let goods = this.goodslist[index];
			uni.showLoading({
				title:"加载中",
				mask:true
			});
			this.postJson({
				path:"/seller_user.jsp",
				data:{
					sellerUid:goods.uid
				},
				success:(res)=>{
					if("ok" == res.data.status){
						let seller = res.data.data;
						uni.makePhoneCall({
							phoneNumber:seller.phone
						});
						
					}else{
						uni.showToast({
							icon:'none',
							title:res.data.msg
						});
					}
				},
				complete:()=>{
					uni.hideLoading();
				}
				
			})
		}
	}
}
